@import url(https://fonts.googleapis.com/css2?family=Cabin+Sketch:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Acme&family=Cabin+Sketch:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;

      height: 100%;
    margin: 0px;
    padding: 0px;
}

html{
  height: 100%;
  width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
    display: flex;
    height: 100%;
}

#root{
    height: 100%;
    bottom:0;
}

.NavBar{
    right: 0;
    width: 50%;
    position: absolute;
    top:0;
}

.NavBar ul{
    display:  flex;
    flex-direction: row;
    justify-content: space-between;
    padding-bottom: 0;
    height: 30px;
    align-items: center;
}

.NavBar li{
    text-align: center;
    margin: 5px auto;
    color: orange;
    list-style: none;
}
.ParallexGrid{
    position: absolute;
    top:70px;
    width:70%;
    height:calc(100% - 70px);
    overflow-y:scroll;
}

.InfoTile{
    background-color:#FFFFFFFF;
    width:100%;
    height:100% ;
}

.tileContent
{
    padding-left: 20px;
    padding-right: 10px;
}
.nameTitle{
    font-family: 'Cabin Sketch', cursive;
    font-size: 9rem;
}

.normalText{
    color: #6c757d;
}

.subHeading{
    font-family: 'Acme', sans-serif;
    font-size: 4rem;
}
.SideBar{
    width:25%;
    right:0;
    height: 100%;
    background-color: #F95700FF;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.ProfilePic
{
    height:40%;
    right: 50%;
    padding-left: 10%;
    padding-right: 10%;
}

.sideBarContent{
    padding-right: 10%;
    padding-left:10%;
}
.MainScroll{
    width:75%;
    right:0;
    background-color: #FFFFFFFF;
    display: flex;
    flex-direction: column;
    height:1vh;
}

.cartoon{
    z-index:900;
    position:absolute;
    bottom:0;
    right:50px;
}
